#Rolling_Number_container{
    font-size: 60px;
    background-color: #003F67;
    height:75px;
    width: 75px;
    margin-left: calc(50% - 37.5px);
    margin-top: 5%;
    
    border-radius: 10px;
    overflow: hidden;
    /* writing-mode:vertical-lr; */
}
.Rolling_Number_num{
    color: white;
    transition: all 0.5s;
    position: relative;
    top:-8px;
}